<template>
  <v-form v-model="valid" ref="form" lazy-validation>
    <v-text-field label="Name" v-model="name" required></v-text-field>
    <v-text-field label="Email" v-model="email" :rules="emailRules" required ></v-text-field>
    <v-text-field label="Password" v-model="password" required></v-text-field>
    <v-text-field name="input-7-1" label="Confirm Password" v-model="confirm_password"></v-text-field>
    <v-btn class="m-2" @click="submit" :disabled="!valid">
      Submit
    </v-btn>
    <v-btn @click="clear">
      Clear
    </v-btn>
  </v-form>
</template>

<script>
import axios from 'axios'


export default {
name: "Register",
  data: () => ({
    valid: true,
    name:'',
    email: '',
    password: '',
    confirm_password: '',
    emailRules: [
      v => !!v || 'Email is required',
      v => /\S+@\S+\.\S+/.test(v) || 'E-mail must be valid'
    ],
  }),
  methods: {
    async submit(){
      if(this.$refs.form.validate()){ //instrucción utilizada para validar el formulario
        //Peticioń HTTP para registrar un usuario
        /*
        * method: post
        * data: is data of form
        * */
        return axios({
          method: 'post',
          data: {
              name: this.name,
              email: this.email,
              password: this.password
          },
          url: '/users/register',
          headers:{ 'Content-Type': 'application/json'}
        }).then( () =>{ //si la petición es ejecutada exitosamente, mostramos mensaje de información y redireccionamos a otr URL
          this.$swal('Great','You have successfully registered','success');
          this.$router.push({name:'Login'});
        }).catch( (error) =>{
          const  message = error.response.data.message;
          this.$swal('Oh oo!', `${message}`, 'error');
        });
        return true;
      }
    },
    clear(){
      this.$refs.form.reset();
    }
  }
}
</script>

